import React, { Component } from 'react';
import {Link} from 'react-router-dom';
class Footer extends Component {
    render() {
        return (
            <div className="tf-modal-content justify-content-end"> 
                <footer id="footer" className="footer footer-s4">
                    <div className="container">
                        <div id="footer-widget" className="footer-widget-s3 border-bottom-s2 position-relative">
                            <h3 className="widget-title larger lt-sp06 text-white text-center">Let’s try me!</h3>
                            <div className="site-list site-list-center text-center">
                                <a href={"https://twitter.com/Shashan41093633"} target="_blank" className="bg-s1"><i className="fa fa-twitter" aria-hidden="true" /></a>
                                <Link to="#" className="bg-s2"><i className="fa fa-behance" aria-hidden="true" /></Link>
                                <Link to="#" className="bg-s3"><span className="icon-dribble" /></Link>
                            </div>
                        </div>
                    </div>
                    <div id="bottom" className="bottom-s3">
                        <div className="container">
                            <div className="row">
                                <div className="col-lg-4">
                                    <div className="copyright lt-sp02">
                                        © Shashank Designs, 2022 All rights reserved.
                                    </div>
                                </div>
                                <div className="col-lg-6">
                                    <ul className="widget-nav-menu color-s2">
                                        <li><a href="/">Home</a></li>
                                        <li><a href="#portfolio">Portfolio</a></li>
                                        <li><a href="#about">About</a></li>
                                        <li><a href="#contact">Contact</a></li>
                                    </ul>
                                </div>
                                <div className="col-lg-2">
                                    <div className="socials-list color-s2">
                                    <a href={"https://www.facebook.com/Shashankshaji/"} target="_blank"><i className="fa fa-facebook" aria-hidden="true" /></a>
                                    <a href={"https://twitter.com/Shashan41093633"} target="_blank"><i className="fa fa-twitter" aria-hidden="true" /></a>
                                    <a href={"https://www.instagram.com/shaxshank.s/?hl=en"} target="_blank"><i className="fa fa-instagram" aria-hidden="true" /></a>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </footer>
            </div>
        );
    }
}

export default Footer;
